@import url("https://fonts.googleapis.com/css?family=Raleway:400,500");

#quote-box {
    font-family: "Raleway", sans-serif;
    border-radius: 3px;
    position: relative;
    width: 450px;
    padding: 40px 50px;
    display: table;
    background-color: gray;
    margin-bottom: 20px;
}

#quote-box .quote-text {
    text-align: center;
    width: 450px;
    height: auto;
    clear: both;
    font-weight: 500;
    font-size: 1.75em;
}
#quote-box .quote-text i {
    font-size: 1em;
    margin-right: 0.4em;
}
#quote-box .quote-author {
    width: 450px;
    height: auto;
    clear: both;
    padding-top: 20px;
    font-size: 1em;
    text-align: right;
}
#quote-box .buttons {
    width: 450px;
    margin: auto;
    display: block;
}
#quote-box .buttons .button {
    height: 38px;
    border: none;
    border-radius: 3px;
    color: #fff;
    background-color: #333;
    outline: none;
    font-size: 0.85em;
    padding: 8px 18px 6px 18px;
    margin-top: 30px;
    opacity: 1;
    cursor: pointer;
}
#quote-box .buttons .button:hover {
    opacity: 0.9;
}
#quote-box .buttons .button#tweet-quote2,
#quote-box .buttons .button#tumblr-quote {
    float: left;
    padding: 0px;
    padding-top: 8px;
    text-align: center;
    font-size: 1.2em;
    margin-right: 5px;
    height: 30px;
    width: 40px;
}
#quote-box .buttons .button#new-quote {
    float: right;
}
